import vars from '../_vars';

console.log('Меню работает');

function openMenu() {
  vars.$hamburger.addEventListener('click', () => {
    vars.$menu.classList.add('active');
    vars.$body.style.overflow = 'hidden';
    vars.$body.style.height = '100vh';
  });
}

function closeMenu() {
  vars.$menuClose.addEventListener('click', (e) => {
    vars.$menu.classList.remove('active');
    vars.$body.style.overflow = '';
    vars.$body.style.height = '';
  });

  vars.$overlay.addEventListener('click', (e) => {
    vars.$menu.classList.remove('active');
    vars.$body.style.overflow = '';
    vars.$body.style.height = '';
  });

  vars.$menuLinks.forEach(item => {
    item.addEventListener('click', (e) => {
      vars.$menu.classList.remove('active');
      vars.$body.style.overflow = '';
      vars.$body.style.height = '';
    });
  });

  document.addEventListener('keydown', function(e) {
    if (e.code == 'Escape') {
      vars.$menu.classList.remove('active');
      vars.$body.style.overflow = '';
      vars.$body.style.height = '';
    }
  });
}

openMenu();
closeMenu();
